

nav {
    display: flex;
    background-color: #ff6600;
    align-items: center;
    padding: 5px 20px;
    border-radius: 3px 3px 0 0;

    justify-content: center;

    margin: 5px 8px;

  }

#id_nav_link_home_active, 
#id_nav_link_spacecrafts_active,
#id_nav_link_planets_active {

    color: rgb(235, 217, 220);
    cursor: pointer;
    margin: 10px 50px;
    border: solid rgba(232, 239, 236, 0.959);
    border-width: 10px;
    border-radius: 5%;
    background-color: rgb(33, 3, 62);

    font-weight: bold;
    font-size:medium;

    width: 120px;
    height:30px;
    text-align: center;

    padding: 1%;
    justify-content: center;
}

#id_nav_link_home_inactive, 
#id_nav_link_spacecrafts_inactive,
#id_nav_link_planets_inactive {

    color: rgb(235, 217, 220);
    cursor: pointer;
    margin: 10px 50px;
    border: solid rgba(3, 66, 36, 0.348);
    border-width: 10px;
    border-radius: 5%;
    background-color: rgb(33, 3, 62);

    font-weight: bold;
    font-size:medium;

    width: 120px;
    height:30px;
    text-align: center;
    text-decoration: none;

    padding: 1%;
    justify-content: center;
}

.class_nav_link_home_active, 
.class_nav_link_spacecrafts_active,
.class_nav_link_planets_active {

    color: rgb(235, 217, 220);
    cursor: pointer;
    margin: 10px 50px;
    border: solid rgba(167, 237, 158, 0.959);
    border-width: 5px;
    border-radius: 5%;
    background-color: rgb(33, 3, 62);

    font-weight: bold;
    font-size:medium;

    width: 120px;
    height:30px;
    text-align: center;
    text-decoration: none;

    justify-content: center;
    padding: 3px;
}

.class_nav_link_home_inactive, 
.class_nav_link_spacecrafts_inactive,
.class_nav_link_planets_inactive {

    color: rgb(235, 217, 220);
    cursor: pointer;
    margin: 10px 50px;

    background-color: rgb(33, 3, 62);

    font-weight: bold;
    font-size:medium;

    width: 120px;
    height:30px;
    text-align: center;
    text-decoration: none;

    justify-content: center;
    padding: 3px;
}